import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import axios from 'axios'
import AOS from 'aos';
import 'aos/dist/aos.css';
class  BlogList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bloglist: [],
            blogTitle: null,
            blogImage: null,
            blogContent: null,
            loading: false,
        }
    };
    getBlogListData() {
        this.setState({ loading: true })
        axios.post('http://localhost:3001/api/bloglist')
            .then(res => {
                const bloglist = res.data;
                this.setState({ bloglist: bloglist.bloglist, loading: false }
                );
            })
            .catch((error) => {
                console.log(error)

            })
    }

    componentDidMount() {
        AOS.init();
        this.getBlogListData();
    }
    componentDidUpdate() {
    }
    render() {
        // if (this.state.blog.length === 0) {
        //     return (
        //         <section id="tour" style={{ marginTop: '10%' }}>
        //             <NotFound></NotFound>
        //         </section >
        //     )
        // }
        console.log(this.state.bloglist)
        return (
            <section id="travel" style={{background: '#222'}} className="">
                <h1 style={{ textAlign: "center", fontWeight: 'bold', paddingTop: '100px', height: '20vh',color:'white' }}>Travel Blog</h1>
                <main className="page-content">
                    {this.state.bloglist.map(item => (

                    <div key={item.idtips} className="card" id="card" style={{backgroundImage:`url(${item.image})`, backgroundSize:'cover'}}>
                        <div className="content">
                            <h2 className="title">{item.title}</h2>
                            <p className="copy">{item.content.slice(0, 25)}. . .</p>
                           <Link to={`blog/${item.title}`}> <button className="btn">Read Now</button></Link>
                        </div>
                    </div>
                   

                    ))}
                    {/* <div className="card">
                        <div className="content">
                            <h2 className="title">Mountain View</h2>
                            <p className="copy">Check out all of these gorgeous mountain trips with beautiful views of, you guessed it, the mountains</p>
                            <button className="btn">View Trips</button>
                        </div>
                    </div>
                    <div className="card">
                        <div className="content">
                            <h2 className="title">To The Beach</h2>
                            <p className="copy">Plan your next beach trip with these fabulous destinations</p>
                            <button className="btn">View Trips</button>
                        </div>
                    </div>
                    <div className="card">
                        <div className="content">
                            <h2 className="title">Desert Destinations</h2>
                            <p className="copy">It's the desert you've always dreamed of</p>
                            <button className="btn">Book Now</button>
                        </div>
                    </div> */}
                </main>
            </section>
        )
    }

}
export default  BlogList