import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

class Navigation extends Component {
    render() {
        return (
            <div className="navbar-section" id="navbar">
                <nav className="navbar navbar-expand-lg navbar-dark bg-brown static-top">
                    <div className="container">
                        <a className="navbar-brand" href="/#">
                            <img src="/images/logo.JPG" alt="logo" height="36" />
                        </a>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul id="nav" className="navbar-nav ms-auto">
                                {/* <li className="current nav-item home">
                                <a className="nav-link" aria-current="page" href="/#"><span data-hover="Trang chủ">Trang chủ</span></a>
                            </li> */}
                                <li className="nav-item home">
                                    <a className="nav-link" aria-current="page" href="/#"><span data-hover="Trang chủ">Trang chủ</span></a>
                                </li>
                                <li className="nav-item about">
                                    <a className="nav-link" href="/#about"><span data-hover="Chúng tôi">Chúng tôi</span></a>
                                </li>
                                <li className="nav-item tour">
                                    <a className="nav-link" href="/#tour"><span data-hover="Hot tour">Hot tour</span></a>
                                </li>

                                <li className="nav-item dropdown">
                                    <a className="nav-link dropdown-toggle" href="#desitination" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        <span data-hover="Điểm đến">Điểm đến</span>
                                    </a>
                                    <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarDropdown">
                                        <li><a className="dropdown-item" href="/tour">Tour nửa ngày</a></li>
                                        <li><a className="dropdown-item" href="/tour">Tour 1 ngày</a></li>
                                        <li><a className="dropdown-item" href="/tour">Tour 2 ngày</a></li>
                                        <li><a className="dropdown-item" href="/tour">Tour 3 ngày</a></li>
                                        <li><a className="dropdown-item" href="/tour">Tour 4 ngày</a></li>
                                        <li><a className="dropdown-item" href="/tour">Tour 5 ngày</a></li>
                                        {/* <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li><a className="dropdown-item" href="#desitination">Something else here</a></li> */}
                                    </ul>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="/blog"> <span data-hover="Travel blogs">Travel blogs</span></a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#footer"><span data-hover="Liên hệ">Liên hệ</span></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>
            </div>

        );
    }
}

export default Navigation