import React, { Component } from 'react'
import axios from 'axios'
import 'aos/dist/aos.css';
import NotFound from './notFound';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Accordion from 'react-bootstrap/Accordion'
import { Redirect } from 'react-router';
import Loader from './loader';
import PageTour from './pageTour';

class ChildHotTour extends Component {
    constructor(props) {
        super(props);
        this.state = {
            info: {
                checkTour: null,
            },
        };
    }
    
    checkTourdata() {
        axios.post('http://localhost:3001/api/maintour/allData')
            .then(res => {
                const tourData = res.data;
                const tour = tourData["tour"];
                const paramName = this.props.match.params.name;
                const titleList = tour.map(item => item.title);
                const checkTour = titleList.find(element => element === paramName)
                console.log('checkTour:',checkTour)
                this.setState({
                    info: {
                        checkTour: checkTour,
                    },
                })
            })
            .catch(error => console.log(error));
        
    }
    componentDidMount() {
        //get if tour exist
        this.checkTourdata();
    }
    render() {
        if (!this.state.info.checkTour) {
            return (
                <section id="childHotTour">
                    <NotFound></NotFound>
                </section >
            )
            // return <Redirect to="/404" />
        }
            return (
                <section id="childHotTour">
                     <PageTour paramID={this.state.info.checkTour}/>
                </section>
              
            );
  
    }
}
export default ChildHotTour