import React, { Component } from 'react'
// import { Link } from 'react-router-dom';
import axios from 'axios'
import AOS from 'aos';
import 'aos/dist/aos.css';
import NotFound from './notFound';
class Blog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blog: [],
            blogTitle: null,
            blogImage: null,
            blogContent: null,
            loading: false,
        }
    };
    async getBlogData() {
        // this.setState({ loading: true })
        const data = this.props.match.params.name;
        await axios.post('http://localhost:3001/api/blog', {
            blog: data,
        })
            .then(res => {
                const blog = res.data;
                this.setState({ blog: blog.blog });
                const titleBlog = this.state.blog.map(item => item.title);
                const image = this.state.blog.map(item => item.image);
                this.setState({
                    blogTitle: titleBlog,
                    blogImage: image,
                })
            })
            .catch(error => console.log(error));
    }
    componentDidMount() {
        AOS.init();
        this.getBlogData();
    }
    componentDidUpdate() {
    }
    render() {
        if (this.state.blog.length === 0) {
            return (
                <section id="tour">
                    <NotFound></NotFound>
                </section >
            )
        }
        return (
            <section id="tour" className="title_travelblogs">
                <div className="travel_space"></div>
                <div className="travel_content">
                    <h1  data-aos="fade-up"
                            data-aos-anchor-placement="top-bottom"  className="travel_title">{this.state.blogTitle}</h1>

                    <div className="travel_img">
                        <img data-aos-once="true" data-aos="fade-up"
                            data-aos-anchor-placement="top-bottom" 
                            alt="travel-blog image" src={this.state.blogImage}></img>
                    </div>
                    <div className="blogs_content">
                        {this.state.blog.map((item, i) => (
                            <div data-aos-once="true"
                             data-aos="zoom-out" key={i}>
                                {item.content.split("\\n").map((subPara, k) => (
                                    <p key={k}>{subPara}</p>))}
                            </div>
                        ))}
                    </div>
                </div>
                <div className="travel_space"></div>
            </section>
        )
    }

}
export default Blog